body {
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

header {
  background: #429ef5;
}

header h1 {
  margin: 0;
  padding: 1rem;
  text-align: center;
}

.card {
  width: 80%;
  margin: 1rem auto;
  max-width: 800px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

article p {
  padding: 2rem;
  font-size: 1.2rem;
}

.main-container {
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  background: #429ef5;
  position: fixed;
  bottom: 0;
  width: 100%;
}

footer p {
  text-align: center;
  margin: 1rem;
}